.addButton {
  background-color: #6c63ff;
  color: white;
  border: 1px solid rgba(128, 128, 128, 0.4);
  border-radius: 50px;
  padding: 4px 14px;
  transition: 0.2s;
  cursor: pointer;
  font-size: 35px;
  margin: 10px;
  position: fixed;
  right: 20%;
  bottom: 10px;
}

.addButton:hover {
  background-color: #5850dd;
}

.popup {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}

.popupContent {
  background-color: white;
  width: 40%;
  text-transform: uppercase;
  text-align: center;
  padding: 20px;
  border-radius: 20px;
}

.popupContent input {
  border: 1px solid #6c63ff;
  outline: none;
  border-radius: 5px;
  width: 93%;
  font-size: 14px;
  padding: 10px;
  opacity: 1;
}

.popupContent input:focus {
  outline: 1px solid #6c63ff;
  color: #6c63ff;
}
.popupContent input::placeholder {
  opacity: 0.5;
}

.popupContent input:focus::placeholder {
  color: #5850dd;
}

.popupContent .buttons {
  margin-top: 120px;
  display: flex;
  justify-content: space-between;
}

.popupContent .buttons button {
  padding: 10px 22px;
  border: 1px solid #6c63ff;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
  transition: 0.4s;
}

.popupContent .buttons button:first-child {
  color: #6c63ff;
  background-color: transparent;
}

.popupContent .buttons button:first-child:hover {
  background-color: rgb(245, 245, 245);
  border-radius: 8px;
}

.popupContent .buttons button:last-child {
  color: white;
  background-color: #6c63ff;
}

.popupContent .buttons button:last-child:hover {
  background-color: #5850dd;
  border-radius: 8px;
}
